#notfound {
    position: relative;
    height: 100vh;

    .notfound {
        position: absolute;
        left: 50%;
        top: 40%;
        -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);

        h2 {
            font-family: "Titillium Web", sans-serif;
            font-size: 26px;
            font-weight: 700;
            margin: 0;
        }
    }

    .notfound404 {
        position: relative;
        height: 150px;
        line-height: 150px;
        margin-bottom: 25px;

        h1 {
            font-family: "Titillium Web", sans-serif;
            font-size: 186px;
            font-weight: 900;
            margin: 0px;
            text-transform: uppercase;
            background-size: cover;
            background-position: center;
        }
    }
}
